import React, { ComponentType, memo, useCallback, useMemo } from "react";
import { FlatList, FlatListProps, ListRenderItemInfo } from "react-native";
import { Album, CameraRoll } from "@react-native-camera-roll/camera-roll";
import { styled, useAsyncEffect, useAutoToastError } from "@true/core";
import { useAppState } from "@react-native-community/hooks";
import AlbumItem from "./AlbumItem";

export const AlbumPicker = memo(function AlbumPicker({
  onAlbumSelect,
}: {
  onAlbumSelect: (album: Album) => any;
}) {
  const appState = useAppState();

  const {
    call: fetchAlbums,
    error: loadAlbumsError,
    loading: loadingAlbums,
    value: _albums,
  } = useAsyncEffect((): Promise<Album[] | undefined> => {
    if (appState !== "active") return Promise.resolve(undefined);

    const getAlbums = require("react-native").NativeModules.RNCCameraRoll
      .getAlbums as unknown as typeof CameraRoll.getAlbums;
    return getAlbums({
      assetType: "All",
    }) as unknown as Promise<Album[] | undefined>;
  }, [appState]);

  useAutoToastError(loadAlbumsError);

  const renderItem = useCallback(
    (info: ListRenderItemInfo<Album>) => {
      return <AlbumItem album={info.item} onPress={onAlbumSelect} />;
    },
    [onAlbumSelect]
  );

  const albums = useMemo(() => {
    const allAlbum = { title: "All", count: 0 };
    return [
      allAlbum,
      ...(_albums || []).sort((a, b) => {
        return b.count - a.count;
      }),
    ];
  }, [_albums]);

  return (
    <SWrapper>
      <List
        data={albums}
        refreshing={loadingAlbums}
        onRefresh={fetchAlbums}
        renderItem={renderItem}
        keyExtractor={keyExtractor}
      />
    </SWrapper>
  );
});

const keyExtractor = (album: Album) => album.title;

const List = styled(FlatList).attrs({
  contentContainerStyle: { flexGrow: 1 },
})`
  flex: 1;
  background-color: ${(p) => p.theme.backgroundColor};
` as unknown as ComponentType<FlatListProps<Album>>;

const SWrapper = styled.View`
  flex: 1;
`;

export default AlbumPicker;
