import { Colors, memoForwardRef, styled } from "@true/core";
import {
  ActivityIndicator,
  GestureResponderEvent,
  ImageProps,
  ImageStyle,
  TextProps,
  TextStyle,
  TouchableOpacity,
  TouchableOpacityProps,
  View,
  ViewStyle,
} from "react-native";
import React, { useCallback, useMemo, useRef } from "react";
import { useButton } from "./context";
import Animated, {
  useSharedValue,
  withDelay,
  withTiming,
} from "react-native-reanimated";
import { TouchableOpacity as RNTouchableOpacity } from "react-native-gesture-handler";

export interface ButtonProps extends TouchableOpacityProps {
  title?: string;
  loading?: boolean;
  style?: ViewStyle;
  textProps?: TextProps;
  textStyle?: TextStyle;
  hideContentWhileLoading?: boolean;

  source?: any;
  imageStyle?: ImageStyle;
  imageProps?: ImageProps;
  imageSize?: number;

  wait?: number;
  isGestureHandler?: boolean;
  loadingColor?: string;

  rightSource?: boolean;
  rightImageStyle?: ImageStyle;
  rightImageProps?: ImageProps;
  rightImageSize?: number;
}

const AnimatedTouchOpacity = Animated.createAnimatedComponent(TouchableOpacity);
const AnimatedRNTouchOpacity =
  Animated.createAnimatedComponent(RNTouchableOpacity);

const hitSlopVal = (imageSize: number) =>
  imageSize < 40 ? (40 - imageSize) / 2 : 0;

export const UIButton = memoForwardRef(
  ({
    title,
    loading,
    textProps,
    textStyle,
    hideContentWhileLoading,
    source,
    imageStyle,
    imageProps,
    imageSize = 24,
    onPress,
    wait = 0,
    isGestureHandler,
    loadingColor,
    rightSource,
    rightImageStyle,
    rightImageProps,
    rightImageSize,
    ...rest
  }: ButtonProps) => {
    const { disabled } = useButton();

    const _disabled = useSharedValue(0);

    const _onPress = useCallback(
      (e: GestureResponderEvent) => {
        if (onPress) {
          // if there is wait
          if (wait !== 0) {
            if (
              !_disabled.value &&
              !disabled?.value &&
              !loading &&
              !rest.disabled
            ) {
              onPress(e);
            }
            _disabled.value = 1;
            if (disabled) disabled.value = 1;

            _disabled.value = withDelay(wait, withTiming(0, { duration: 0 }));
            if (disabled)
              disabled.value = withDelay(wait, withTiming(0, { duration: 0 }));
            return;
          }

          // if there is no wait
          onPress(e);
        }
      },
      [wait, _disabled, disabled, loading, rest.disabled, onPress]
    );

    const _Comp = useMemo(
      () => (isGestureHandler ? AnimatedRNTouchOpacity : AnimatedTouchOpacity),
      [isGestureHandler]
    );

    return (
      <_Comp
        hitSlop={{
          top: hitSlopVal(imageSize),
          left: hitSlopVal(imageSize),
          right: hitSlopVal(imageSize),
          bottom: hitSlopVal(imageSize),
        }}
        {...rest}
        onPress={_onPress}
      >
        <View
          style={{
            flexDirection: "row",
            opacity: hideContentWhileLoading && loading ? 0 : 1,
            alignItems: "center",
          }}
        >
          {source ? (
            <Animated.Image
              source={source}
              style={[
                imageStyle,
                {
                  width: imageSize,
                  height: imageSize,
                },
              ]}
              {...imageProps}
            />
          ) : null}
          {title ? (
            <SText style={textStyle} {...textProps}>
              {title}
            </SText>
          ) : null}
          {rightSource ? (
            <Animated.Image
              source={rightSource}
              style={[
                rightImageStyle,
                {
                  width: rightImageSize,
                  height: rightImageSize,
                },
              ]}
              {...rightImageProps}
            />
          ) : null}
        </View>
        <View
          style={{
            position: "absolute",
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
            alignItems: "center",
            justifyContent: "center",
            opacity: loading ? 1 : 0,
          }}
        >
          <ActivityIndicator color={loadingColor} />
        </View>
        {rest.children}
      </_Comp>
    );
  }
);

const SText = styled.Text`
  color: ${Colors.neutral1};
`;

export * from "./BottomButton";
