import { FileType } from "@true/core";
import { useMemo } from "react";
import constate from "constate";

const _provider = ({ selected }: { selected?: FileType[] }) => {
  return useMemo(
    () => ({
      selected,
    }),
    [selected]
  );
};

export const [CamProvider, useCamProvider] = constate(_provider, (val) => val);
