import React, { RefObject, useCallback, useMemo } from "react";
import { memoForwardRef, useTheme, ETheme } from "@true/core";
import {
  BottomSheetBackdrop,
  BottomSheetBackdropProps,
  BottomSheetModal,
  BottomSheetModalProps,
} from "@gorhom/bottom-sheet";
import { useBottomSpace } from "./hooks";

export interface MyBottomSheetProps extends BottomSheetModalProps {
  hasFlashList?: boolean;
  backDropProps?: {
    opacity?: number;
  };
}

export const MyBottomSheet = memoForwardRef(
  (props: MyBottomSheetProps, ref: RefObject<BottomSheetModal>) => {
    const { negativeABottom, aTop } = useBottomSpace();

    const renderBackDrop = useCallback((_props: BottomSheetBackdropProps) => {
      return (
        <BottomSheetBackdrop
          {..._props}
          onPress={() => {
            ref.current?.dismiss();
          }}
          appearsOnIndex={0}
          disappearsOnIndex={-1}
          isCustomAnimatedIndex={props.hasFlashList}
          {...props.backDropProps}
        />
      );
    }, []);

    const theme = useTheme();

    return (
      <BottomSheetModal
        ref={ref}
        keyboardOffset={negativeABottom}
        topInset={aTop}
        keyboardBlurBehavior={"restore"}
        android_keyboardInputMode={"adjustPan"}
        backdropComponent={renderBackDrop}
        backgroundStyle={{ backgroundColor: theme.backgroundColor }}
        handleStyle={{
          backgroundColor: theme.backgroundColor,
          borderTopLeftRadius: 14,
          borderTopRightRadius: 14,
        }}
        handleIndicatorStyle={{ backgroundColor: theme.neutral2 }}
        {...props}
      >
        {props.children}
      </BottomSheetModal>
    );
  }
);

export * from "./hooks";
export * from "./BottomSheetView";
export * from "./BottomSheetFlashList";
