import {
  CameraRoll,
  cameraRollEventEmitter,
  GroupTypes,
  PhotoIdentifier,
} from "@react-native-camera-roll/camera-roll";

import { useCallback, useEffect, useRef, useState } from "react";

import { AppState, EmitterSubscription, Platform } from "react-native";
import { FileType } from "@true/core";

export const isIOS = Platform.OS === "ios";

export const isAndroid = Platform.OS === "android";

export const isAboveIOS14 =
  isIOS && parseInt(Platform.Version as string, 10) >= 14;

interface GalleryOptions {
  pageSize: number;
  groupName: string;
  groupTypes: GroupTypes;
}

interface GalleryLogic {
  photos?: FileType[];
  loadNextPagePictures: () => void;
  refresh: () => void;
  isLoading: boolean;
  isLoadingNextPage: boolean;
  isReloading: boolean;
  hasNextPage: boolean;
  isLimited: boolean;
}

/**
 * Convert camera roll photos in FileType format
 *
 * @param array - Array of camera roll format images
 * @returns Array pf pictures in imageDTO format
 */
export const convertCameraRollPicturesToImageDtoType = (
  array: PhotoIdentifier[]
): FileType[] => {
  return array.map((item) => {
    const _ = item.node.image.filename || "";
    const pos = _.lastIndexOf(".") + 1;
    const ext = _.substring(pos < 0 ? _.length : pos);
    const name = item.node.type.startsWith("image")
      ? _.substring(0, pos < 0 ? _.length : pos) + "jpg"
      : _;

    return {
      size: item.node.image.fileSize,
      type: item.node.type,
      playableDuration: item?.node?.image?.playableDuration || 0,
      duration: item?.node?.image?.playableDuration || 0,
      name: name.replace(/\s/g, "-"),
      ext: item.node.type.startsWith("image") ? "jpg" : ext,
      height: item.node.image.height,
      width: item.node.image.width,
      uri: item.node.image.uri,
      path: item.node.image.uri,
    };
  });
};

export const useGallery = ({
  pageSize = 30,
  groupName,
  groupTypes,
}: GalleryOptions): GalleryLogic => {
  const [isLoading, setIsLoading] = useState(false);
  const [isReloading, setIsReloading] = useState(false);
  const [isLoadingNextPage, setIsLoadingNextPage] = useState(false);
  const [hasNextPage, setHasNextPage] = useState(false);
  const nextCursor = useRef<string | undefined>(undefined);
  const [photos, setPhotos] = useState<FileType[]>([]);
  const [isLimited, setIsLimited] = useState(false);
  const loadingNextPagePicturesRef = useRef(false);

  const loadNextPagePictures = useCallback(async () => {
    if (isLoading || isLoadingNextPage || loadingNextPagePicturesRef.current) {
      return;
    }

    try {
      loadingNextPagePicturesRef.current = true;
      nextCursor.current ? setIsLoadingNextPage(true) : setIsLoading(true);
      const { edges, page_info, limited } = await CameraRoll.getPhotos({
        first: pageSize,
        after: nextCursor.current,
        assetType: "All",
        groupName: groupName,
        groupTypes,
        ...(isAndroid && { include: ["filename", "playableDuration"] }),
      });
      if (limited) setIsLimited(limited);
      const newPts = convertCameraRollPicturesToImageDtoType(edges);
      setPhotos((prev) => [...(prev || []), ...newPts]);

      nextCursor.current = page_info.end_cursor;
      setHasNextPage(page_info.has_next_page);
    } catch (error) {
      console.error("useGallery getPhotos error:", error);
    } finally {
      setIsLoading(false);
      setIsLoadingNextPage(false);
      loadingNextPagePicturesRef.current = false;
    }
  }, [nextCursor, groupName, groupTypes, pageSize]);

  const getUnloadedPictures = useCallback(async () => {
    if (loadingNextPagePicturesRef.current || isReloading) {
      return;
    }
    try {
      setIsReloading(true);
      loadingNextPagePicturesRef.current = true;
      const { edges, page_info, limited } = await CameraRoll.getPhotos({
        first: pageSize,
        assetType: "All",
        groupName: groupName,
        groupTypes: groupTypes,
        after: undefined,
        // Include fileSize only for android since it's causing performance issues on IOS.
        ...(isAndroid && { include: ["filename", "playableDuration"] }),
      });
      const newPhotos = convertCameraRollPicturesToImageDtoType(edges);
      setPhotos(newPhotos);
      if (limited) setIsLimited(limited);
      nextCursor.current = page_info.end_cursor;
      setHasNextPage(page_info.has_next_page);
    } catch (error) {
      console.error("useGallery getNewPhotos error:", error);
    } finally {
      setIsReloading(false);
      loadingNextPagePicturesRef.current = false;
    }
  }, [pageSize, groupName, groupTypes, isReloading]);

  const refresh = useCallback(async () => {
    setIsLoading(true);
    try {
      nextCursor ? setIsLoadingNextPage(true) : setIsLoading(true);
      const { edges, page_info, limited } = await CameraRoll.getPhotos({
        first: pageSize,
        after: "",
        assetType: "All",
        groupName,
        groupTypes,
        ...(isAndroid && { include: ["playableDuration", "filename"] }),
      });
      const _photos = convertCameraRollPicturesToImageDtoType(edges);
      setPhotos([..._photos]);
      if (limited) setIsLimited(limited);
      nextCursor.current = page_info.end_cursor;
      setHasNextPage(page_info.has_next_page);
    } catch (error) {
      console.error("useGallery refresh error:", error);
    } finally {
      setIsLoading(false);
      setIsLoadingNextPage(false);
    }
  }, [pageSize, groupTypes, groupName]);

  useEffect(() => {
    if (!photos?.length) {
      loadNextPagePictures().then();
    }
  }, [photos]);

  useEffect(() => {
    const subscription = AppState.addEventListener(
      "change",
      async (nextAppState) => {
        if (nextAppState === "active") {
          getUnloadedPictures().then();
        }
      }
    );

    return () => {
      subscription.remove();
    };
  }, [getUnloadedPictures]);

  // useEffect(() => {
  //   let subscription: EmitterSubscription;
  //   if (isAboveIOS14) {
  //     subscription = cameraRollEventEmitter.addListener(
  //       "onLibrarySelectionChange",
  //       (_event) => {
  //         getUnloadedPictures().then();
  //       }
  //     );
  //   }
  //
  //   return () => {
  //     if (isAboveIOS14 && subscription) {
  //       subscription.remove();
  //     }
  //   };
  // }, [getUnloadedPictures]);

  return {
    refresh,
    photos,
    loadNextPagePictures,
    isLoading,
    isLoadingNextPage,
    isReloading,
    hasNextPage,
    isLimited,
  };
};
