import {
  runOnUI,
  useDerivedValue,
  useSharedValue,
} from "react-native-reanimated";
import { LayoutChangeEvent } from "react-native";
import { useCallback, useRef } from "react";
import { interactManager } from "@true/core";

/**
 *
 * @param initSnapPoints eg: [25%, 'CONTENT_HEIGHT + 24'] *note: 24 is extra height
 * @param deps
 */
export const useAnimatedSnapPoints = (
  initSnapPoints?: (number | string)[],
  deps?: any[]
) => {
  const animatedListHeight = useSharedValue<number>(0);
  const animatedViewHeight = useSharedValue<{ [id: string]: number }>({});
  const animatedSnapPoints = useDerivedValue(() => {
    let _animatedHeight =
      animatedListHeight.value +
      Object.values(animatedViewHeight.value).reduce(
        (partialSum, a) => partialSum + a,
        0
      ) +
      24;

    return initSnapPoints
      ? initSnapPoints.map((item) => {
          if (typeof item == "string") {
            const isMatch = item.indexOf("CONTENT_HEIGHT") !== -1;
            const isPositive = item.indexOf("+")
              ? 1
              : item.indexOf("-")
              ? -1
              : 0;
            const extra =
              isPositive == 1
                ? Number(item.split("+")[1])
                : isPositive == -1
                ? Number(item.split("-")[1])
                : 0;
            if (isMatch) return _animatedHeight + extra;
          }

          return item;
        })
      : [_animatedHeight];
  }, [...(deps || []), initSnapPoints, animatedListHeight, animatedViewHeight]);

  /**
   * onLayout for BottomSheetView, which indentified by 'key'
   * */

  const uiCallback = useCallback((key: string, h: number) => {
    "worklet";
    animatedViewHeight.value = {
      ...animatedViewHeight.value,
      [key]: h,
    };
  }, []);

  const onViewLayout = useCallback(
    (key: string) => (e: LayoutChangeEvent) => {
      const h = e.nativeEvent.layout.height;
      runOnUI(uiCallback)(key, h);
    },
    [animatedViewHeight]
  );

  const timout = useRef<any>(null);
  const count = useSharedValue(0);

  /**
   * onContentSize for list components (such as FlatList)
   * */
  const onListContentSize = useCallback(
    (w: number, h: number) => {
      animatedListHeight.value = Math.round(h);

      // if (h > 1) {
      //   const _h = Math.round(h);
      //   if (count.value === 0) {
      //     animatedListHeight.value = _h;
      //   } else {
      //     interactManager(
      //       () => {
      //         animatedListHeight.value = _h;
      //       },
      //       500,
      //       true,
      //       timout
      //     );
      //   }
      //   count.value++;
      // }
    },
    [animatedListHeight, timout]
  );

  /**
   * onLayout for list components (such as FlatList)
   * */
  const onListLayout = useCallback(
    (e: LayoutChangeEvent) => {
      // @ts-ignore
      e.currentTarget.getInnerViewRef().measure((x, y, w, h) => {
        animatedListHeight.value = h;
      });
    },
    [animatedListHeight]
  );

  return {
    animatedSnapPoints,
    onViewLayout,
    onListContentSize,
    onListLayout,
    animatedListHeight,
    animatedViewHeight,
  };
};
