import React, { memo, useCallback } from "react";
import { Text } from "react-native";
import { ListItem } from "./ListItem";
import { Album } from "@react-native-camera-roll/camera-roll";

export const AlbumItem = memo(function AlbumItem({
  album,
  onPress,
}: {
  album: Album;
  onPress: (album: Album) => any;
}) {
  const isAll = album.title === "All";

  return (
    <ListItem
      onPress={useCallback(() => onPress(album), [onPress, album])}
      title={isAll ? "all" : album.title}
      rightContent={<Text>{isAll ? "" : album.count}</Text>}
    />
  );
});

export default AlbumItem;
