import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";
import { useAnimatedStyle, useDerivedValue } from "react-native-reanimated";

export const useBottomSpace = () => {
  const { aBottom, aTop } = useAnimatedSafeAreaInsets();
  const negativeABottom = useDerivedValue(() => -aBottom.value, [aBottom]);
  const animatedStyles = useAnimatedStyle(() => ({
    paddingBottom: aBottom.value,
  }));

  return {
    aTop,
    aBottom,
    negativeABottom,
    animatedStyles,
  };
};
