import React, {
  ReactElement,
  RefObject,
  useCallback,
  useImperativeHandle,
  useMemo,
  useRef,
} from "react";
import {
  ActionSheetIOS,
  ActivityIndicator,
  Dimensions,
  LayoutChangeEvent,
  Linking,
  Platform,
} from "react-native";
import { iosRefreshGallerySelection } from "@react-native-camera-roll/camera-roll";
import { Row } from "./Row";
import {
  FileType,
  Fonts,
  memoForwardRef,
  styled,
  ToastService,
  useTheme,
  Colors,
} from "@true/core";
import { UIButton } from "../UIButton";
import { BottomSheetFlashList } from "../MyBottomSheet";
import { FlashList } from "@shopify/flash-list";
import { useGallery } from "./useGallery";
import { useSharedValue } from "react-native-reanimated";
import { useGalleryPermission } from "./useGalleryPermission";
import RNFS from "react-native-fs";
import { useMounted } from "@true/core";
import { CamProvider, useCamProvider } from "./provider";
import { openSettings } from "react-native-permissions";

const MAX_HEIGHT = 1000;

enum CameraRollAssetType {
  Photos = "Photos",
  Videos = "Videos",
  All = "All",
}

interface CameraRollPickerProps {
  initialNumToRender?: number;
  groupTypes?: any;
  assetType?: CameraRollAssetType;
  selectSingleItem?: boolean;
  emptyText?: string;
  loader?: ReactElement;
  groupName?: string;
  selected?: FileType[];
  onSelectImage?: (image: FileType) => void;
  onSelectImages?: (images: FileType[]) => void;
  isMarked?: boolean;
  isBottomSheet: boolean;
  maxFiles?: number;
  toastShowMaxFile?: number;
}

export interface CameraRollPickerRef {
  onImagePress: (image: FileType) => void;
  clearImages: () => void;
}

const _CameraRollPicker = memoForwardRef(
  (props: CameraRollPickerProps, ref: RefObject<CameraRollPickerRef>) => {
    const {
      emptyText,
      groupName,
      onSelectImage,
      onSelectImages,
      // selected,
      groupTypes,
      isMarked,
      isBottomSheet,
      maxFiles,
      toastShowMaxFile,
    } = props;

    const { selected } = useCamProvider();

    const {
      photos,
      loadNextPagePictures,
      hasNextPage,
      isLoadingNextPage,
      isLimited,
      refresh,
    } = useGallery({
      pageSize: 24,
      // @ts-ignore
      groupName,
      groupTypes,
    });
    const itemSize = useSharedValue(0);
    const selectedItemList = useRef(selected);

    const CustomFlatList = useMemo(() => {
      return isBottomSheet ? BottomSheetFlashList : FlashList;
    }, [isBottomSheet]);

    const theme = useTheme();

    const { readPermissionStatus, refreshPermissions, isLoading } =
      useGalleryPermission({
        autoRequestPermission: true,
      });

    const isPermittedToAccessGallery = useMemo(
      () =>
        readPermissionStatus === "granted" ||
        readPermissionStatus === "limited",
      [readPermissionStatus]
    );

    const changePermission = useCallback(() => {
      ActionSheetIOS.showActionSheetWithOptions(
        {
          options: ["Cancel", "Add more photos", "Change setting permission"],
          cancelButtonIndex: 0,
          userInterfaceStyle: theme.name == "dark" ? "dark" : "light",
        },
        (buttonIndex) => {
          if (buttonIndex === 0) {
            // cancel action
          } else if (buttonIndex === 1) {
            iosRefreshGallerySelection().then();
          } else if (buttonIndex === 2) {
            Linking.openSettings().then();
          }
        }
      );
    }, []);

    const onLayout = useCallback((e: LayoutChangeEvent) => {
      const _width =
        e.nativeEvent?.layout?.width || Dimensions.get("window").width;
      itemSize.value = (_width - 3) / 3;
    }, []);

    const onSelectItem = useCallback(
      async (item: FileType) => {
        if (selectedItemList.current) {
          try {
            const newList = [...selectedItemList.current].filter((_item) => {
              return _item.uri !== item.uri;
            });

            /* pick images */
            if (newList.length === selectedItemList.current.length) {
              if (maxFiles && selectedItemList.current?.length + 1 > maxFiles) {
                return ToastService.simpleShow(
                  `max number of files is ${toastShowMaxFile || maxFiles}`
                );
              }

              selectedItemList.current = [...selectedItemList.current, item];
            } else {
              /* unpick images */
              selectedItemList.current = newList;
            }
            onSelectImage && onSelectImage(item);
            onSelectImages?.(selectedItemList.current);
          } catch (e) {
            ToastService.simpleShow(e.message);
          }
        }
      },
      [onSelectImage, onSelectImages, selectedItemList, maxFiles]
    );

    const clearImages = useCallback(() => {
      selectedItemList.current = [];
    }, [selectedItemList.current]);

    useImperativeHandle(ref, () => ({
      onImagePress: onSelectItem,
      clearImages,
    }));

    const renderRow = useCallback(
      ({ item, index }: { item: FileType; index: number }) => {
        return (
          <Row
            key={index.toString()}
            isMarked={isMarked}
            // isSelected={isSelected}
            onSelectItem={onSelectItem}
            photo={item}
            imageSize={itemSize}
            index={index}
            onCameraPicker={refresh}
          />
        );
      },
      [selected, onSelectItem, itemSize]
    );

    // @ts-ignore
    const data: FileType[] = useMemo(() => {
      return [{ uri: "add-media", date: new Date(), isButton: true }].concat(
        // @ts-ignore
        photos as any
      );
    }, [photos]);

    const ListFooter = useCallback(() => {
      if (hasNextPage && isLoadingNextPage) {
        return (
          <ActivityIndicator
            size="large"
            color={"#007AFF"}
            style={{
              alignSelf: "center",
            }}
          />
        );
      }
      return null;
    }, [hasNextPage, isLoadingNextPage]);

    const mounted = useMounted(1);

    return (
      // @ts-ignore
      <SWrapper onLayout={onLayout}>
        {isLimited ? (
          <SLimitedPhotoWrapper onPress={changePermission} activeOpacity={0.9}>
            <STextEditLimitedPhotos>
              Edit Permission Photos{" "}
              <STextButton>Change Permission</STextButton>
            </STextEditLimitedPhotos>
          </SLimitedPhotoWrapper>
        ) : null}
        {readPermissionStatus === "blocked" ||
        (readPermissionStatus === "denied" && Platform.OS === "ios") ? (
          <SOpenSettingButton
            title={
              "Your Gallery access permission is blocked, open setting to provide media permission"
            }
            onPress={openSettings}
          />
        ) : null}
        {mounted ? (
          <CustomFlatList
            ListFooterComponent={ListFooter}
            renderItem={renderRow}
            keyExtractor={keyExtractor}
            data={data}
            // @ts-ignore
            extraData={photos || selected.length || itemSize}
            onEndReachedThreshold={0.01}
            onEndReached={
              photos?.length < 24 || !hasNextPage
                ? undefined
                : loadNextPagePictures
            }
            estimatedItemSize={(Dimensions.get("window").width - 3) / 3}
            numColumns={3}
            ListEmptyComponent={
              <SEmptyWrapper>
                <SEmptyText>{emptyText}</SEmptyText>
              </SEmptyWrapper>
            }
          />
        ) : null}
      </SWrapper>
    );
  }
);

export const CameraRollPicker = memoForwardRef(
  (props: CameraRollPickerProps, ref: RefObject<CameraRollPickerRef>) => {
    return (
      <CamProvider selected={props.selected}>
        <_CameraRollPicker {...props} ref={ref} />
      </CamProvider>
    );
  }
);

const SWrapper = styled.View`
  flex: 1;
  background-color: ${(p) => p.theme.backgroundColor};
`;

const SEmptyWrapper = styled.View`
  flex: 1;
  justify-content: center;
  background-color: ${(p) => p.theme.backgroundColor};
`;
const SEmptyText = styled.Text`
  color: ${(p) => p.theme.neutral1};
  font-size: 16px;
  text-align: center;
`;

const keyExtractor = (item: FileType, index: number) => {
  return index.toString();
};

const SLimitedPhotoWrapper = styled.TouchableOpacity`
  background-color: rgba(240, 247, 255, 1);
  padding: 6px 8px;
  border: 1px solid rgba(224, 224, 224, 1);
  margin-top: -5px;
`;

const STextEditLimitedPhotos = styled.Text`
  font-family: ${Fonts.Regular};
  font-style: normal;
  font-size: 13px;
  line-height: 20px;
  color: #242424;
`;

const STextButton = styled.Text`
  font-family: ${Fonts.Bold};
  font-style: normal;
  font-size: 13px;
  line-height: 20px;
  color: ${(p) => p.theme.primaryColor};
  text-decoration: underline;
  text-decoration-color: ${(p) => p.theme.primaryColor};
`;

const SOpenSettingButton = styled(UIButton).attrs((p) => ({
  textStyle: { color: Colors.blue1 },
}))`
  padding: 8px 12px;
  background-color: ${Colors.blue3};
`;

export * from "./AlbumItem";
export * from "./Row";
export * from "./AlbumPicker";
export * from "./useGallery";
export * from "./ImageItem";
export * from "./ListItem";
