import React, { ReactNode, useMemo } from "react";
import { Fonts, styled } from "@true/core";
import {
  StyleSheet,
  TextProps,
  TextStyle,
  TouchableOpacity,
  TouchableOpacityProps,
  View,
} from "react-native";

export const ListItem = ({
  left,
  leftContent,
  center,
  right,
  rightContent,
  label,
  title,
  titleStyle,
  subtitle,
  children,
  subTitleProps,
  canPress,
  ...props
}: {
  left?: ReactNode;
  leftContent?: ReactNode;
  center?: ReactNode;
  right?: ReactNode;
  rightContent?: ReactNode;
  label?: string | ReactNode;
  title?: string | ReactNode;
  subtitle?: string | ReactNode;
  subTitleProps?: TextProps;
  titleStyle?: TextStyle;
  children?: ReactNode;
  canPress?: boolean;
} & TouchableOpacityProps) => {
  const _styles = useMemo(() => {
    return [styles.wrapper, props.style];
  }, [props.style]);

  const ItemPress: any =
    typeof canPress === "undefined" || canPress === true
      ? TouchableOpacity
      : View;

  return (
    <>
      <ItemPress {...props} style={_styles}>
        {left ? (
          left
        ) : leftContent ? (
          <ListItem.Left>{leftContent}</ListItem.Left>
        ) : null}
        {center ? (
          center
        ) : (
          <ListItem.Center>
            <TextWrapper>
              {label ? <ListItem.Label>{label}</ListItem.Label> : null}
              {typeof title === "object" ? (
                title
              ) : title ? (
                <ListItem.Title style={titleStyle}>{title}</ListItem.Title>
              ) : null}
              {typeof subtitle === "object" ? (
                subtitle
              ) : subtitle ? (
                <ListItem.Subtitle {...(subTitleProps || {})}>
                  {subtitle}
                </ListItem.Subtitle>
              ) : null}
            </TextWrapper>
            {children}
          </ListItem.Center>
        )}
        {right ? (
          right
        ) : rightContent ? (
          <ListItem.Right>{rightContent}</ListItem.Right>
        ) : null}
      </ItemPress>
    </>
  );
};

const styles = StyleSheet.create({
  wrapper: {
    flexDirection: "row",
    padding: 8,
  },
});

const TextWrapper = styled.View`
  justify-content: center;
  min-height: 40px;
`;

ListItem.Left = styled.View`
  width: 40px;
  height: 40px;
  align-items: center;
  justify-content: center;
  flex-direction: row;
`;

ListItem.Center = styled.View`
  flex: 1;
  margin: 0 8px;
  padding-left: 4px;
  justify-content: center;
`;

ListItem.Right = ListItem.Left;

ListItem.Label = styled.Text`
  font-size: 12px;
  line-height: 20px;
  color: ${(p) => p.theme.neutral3};
`;

ListItem.Title = styled.Text`
  font-size: 17px;
  line-height: 20px;
  color: ${(p) => p.theme.neutral1};
`;

ListItem.Subtitle = styled.Text`
  font-size: 13px;
  font-family: ${Fonts.Regular};
  line-height: 20px;
  color: ${(p) => p.theme.neutral3};
`;
