import {BottomSheetView as RNBottomSheetView} from "@gorhom/bottom-sheet"
import {memoForwardRef} from "@true/core"
import {View, ViewProps} from "react-native";
import React, {RefObject} from "react";

export const BottomSheetView = memoForwardRef(({style, onLayout, children,...rest}: ViewProps, ref: RefObject<View>) => {
  return <RNBottomSheetView style={([style, {overflow: "scroll"}].flat()) as any}>
    <View onLayout={onLayout}>
      {children}
    </View>
  </RNBottomSheetView>
})
